(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('UpdateUserProfileController', UpdateUserProfileController);

  /** @ngInject */
  function UpdateUserProfileController(
    $log,
    userProfile,
    cities
  ) {
    var vm = this;
    vm.cities = cities;

    vm.datePicker = {
      mode: 'year',
      dateOptions: {
        datepickerMode: 'year',
        minMode: "'year'",
        // minDate: "minDate",
        showWeeks: "false"
      },
      status: {
        opened: false
      },
      format: 'yyyy'
    };

    vm.openDatePicker = function($event) {
      $event.preventDefault();
      $event.stopPropagation();
      vm.datePicker.status.opened = true;
    };

    // In the begining, it gets the volunteer's profile
    angular.element(document).ready(getProfile());

    function getProfile() {
      var doneCallbacks = function(value) {
          vm.profile = value.data;

          $log.debug(vm.profile);
        },
        failCallbacks = function(response) {
          $log.debug(response);
        };

      userProfile
        .get()
        .then(doneCallbacks, failCallbacks);
    }

    vm.updateAboutAndContact = function() {
      var onSuccess = function(value) {
        $log.debug(value);
        vm.profile = value.data;

        vm.alert = [];
        vm.alert.push({
          type: 'success',
          data: {
            message: 'alert.success.updated_successfully'
          }
        });
      };

      $log.debug(vm.profile);

      userProfile
        .update(vm.profile)
        .then(onSuccess);
    };

    vm.selectAvatar = function(event, fileReader, file, fileList, fileObjects, object) {
      var base64Image = "data:" + object.filetype + ';base64,' + object.base64;

      userProfile
        .updateAvatar(base64Image, true)
        .then(function(value) {
          var data = value.data;

          vm.profile.avatar_url = data.avatar_url;
        })

      vm.avatar = base64Image;
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('ShowUserProfileController', ShowUserProfileController);

  /** @ngInject */

  function ShowUserProfileController($uibModal, cities, $log, defaultAvatarPath, userProfile) {

    var vm = this;

    vm.open = function() {
      var modal = $uibModal.open({
        templateUrl: 'myModalContent.html',
        controller: 'ModalInstanceCtrl',
        controllerAs: 'vm'
      });
    };

    angular.element(document).ready(getProfile());

    function getProfile() {
      var doneCallbacks = function(result) {
          $log.debug("getProfile() done");
          $log.debug(result);

          vm.profile = result.data;
        },
        failCallbacks = function(response) {
          $log.debug(response);
        };

      userProfile.get().then(doneCallbacks, failCallbacks)
    }
  }

  angular.module('vmsFrontend').controller('ModalInstanceCtrl', function($state, $uibModalInstance, $log, vmsLocalStorage, userProfile) {

    var vm = this;
    vm.remind = "提醒您，帳號刪除後無法再還原，如果您確定要刪除帳號。";

    vm.ok = function() {
      $log.debug('deleteClickOk');

      var credentials = {
        username: vmsLocalStorage.getUsername(),
        password: vm.password
      };

      $log.debug("username = " + credentials.username);
      $log.debug("password = " + credentials.password);

      userProfile
        .drop(credentials)
        .then(function(response) {
          $log.debug("delete success");
          $uibModalInstance.close();

          $state.go('login');
        })
        .catch(function(response) {
          $log.debug("delete failure");
          $log.debug(response);

          if (response.status == 401) {
            vm.deleteMsg = "您的密碼輸入錯誤，請重新輸入";
          }
        });
    };

    vm.cancel = function() {
      $uibModalInstance.dismiss('cancel');
    };
  });

})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('VerifyUserEmailController', VerifyUserEmailController);

  /** @ngInject */
  function VerifyUserEmailController($log, $location, vmsClient, authPrinciple, $state, $rootScope, $timeout) {
    var vm = this;
    vm.verification = true;
    vm.verificationSuccess = false;
    vm.verificationStatus = vm.verification || vm.verificationSuccess;

    $log.debug("vm.verification " + vm.verification);
    $log.debug("vm.verificationSuccess " + vm.verificationSuccess);
    $log.debug("vm.verificationStatus " + vm.verificationStatus);

    vm.volunteer = $location.search();

    if (angular.isDefined(vm.volunteer.email) && angular.isDefined(vm.volunteer.verification_token)) {

      angular.element(document).ready(verify());
    } else {
      $log.debug("verification false");
      vm.verification = false;
      vm.verificationStatus = vm.verification || vm.verificationSuccess;

      $log.debug("vm.verificationStatus " + vm.verificationStatus);
    }

    function verify() {
      $log.debug("EmailVerificationController verify()");

      $log.debug("email = " + vm.volunteer.email);
      $log.debug("verification_token = " + vm.volunteer.verification_token);

      var successCallback = function(response) {
        $log.debug('emailVerification success');
        $log.debug(response);

        vm.verification = false;
        vm.verificationSuccess = true;
        vm.verificationStatus = vm.verification || vm.verificationSuccess;

        $timeout(function() {
          $state.go('profile')
        }, 5000);
      };

      var failureCallback = function(response) {
        $log.debug('emailVerification error');
        $log.debug(response);

        vm.verification = false;
        vm.verificationStatus = vm.verification || vm.verificationSuccess;
      };

      vmsClient.emailVerification(vm.volunteer.email,
        vm.volunteer.verification_token)
        .then(successCallback)
        .catch(failureCallback);
    }
  }
})();

