(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('ProjectDetailController', ProjectDetailController);

  /** @ngInject */
  function ProjectDetailController(
    $log,
    $state,
    $stateParams,
    vmsClient,
    project,
    PERMISSION_OPTIONS) {
    var vm = this;
    vm.permissionOptions = PERMISSION_OPTIONS;

    vm.projectId = $stateParams.id;

    angular.element(document).ready(getProject(vm.projectId));
    angular.element(document).ready(getProjectMembers(vm.projectId));

    vm.isGuest = function() {
      if (angular.isDefined(vm.project)) {
        return vm.meta.role.name == 'guest';
      }

      return false;
    };

    vm.isCreator = function() {
      $log.debug('isCreator()');
      if (angular.isDefined(vm.project)) {
        return vm.meta.role.name == 'creator';
      }

      return false;
    };

    vm.isMember = function() {
      if (angular.isDefined(vm.project)) {
        return vm.meta.role.name == 'member';
      }

      return false;
    }

    vm.attend = function() {
      var onSuccess = function(response) {
        $log.debug('attendProject() success');
        $state.reload();
      };
      var onFailure = function(response) {
        $log.debug('attendProject() failure');
      }

      vmsClient.attendProject(vm.projectId)
        .then(onSuccess)
        .catch(onFailure);
    }

    function getProject(id) {
      $log.debug("getProject()");

      var onSuccess = function(value) {
        vm.project = value.data;
        vm.meta = value.meta;
      };
      var onFailure = function(response) {
        $log.debug(response);
      };

      project
        .getById(id)
        .then(onSuccess)
        .catch(onFailure);
    }

    function getProjectMembers(id) {
      var onSuccess = function(response) {
        vm.membersList = response.data;
        $log.debug(response.data);
      };
      var onFailure = function(response) {
        $log.debug(response);
      };

      vmsClient.getProjectMembers(id)
        .then(onSuccess)
        .catch(onFailure);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('ManageProjectCustomFieldsController', ManageProjectCustomFieldsController);

  /** @ngInject */
  function ManageProjectCustomFieldsController(
    $log,
    $stateParams,
    $uibModal,
    projectCustomField
  ) {
    var vm = this;
    var projectId = $stateParams.projectId;

    angular.element(document).ready(getProjectCustomFields);

    vm.preview = preview;
    vm.appendCustomField = appendCustomField;

    function getProjectCustomFields() {
      projectCustomField
        .getAllByProjectId(projectId)
        .then(function(value) {
          if (value.data.length == 0) {
            vm.alerts = [
              {
                type: 'info',
                data: {
                  message: 'alert.info.non_custom_field'
                }
              }
            ];
          }

          vm.customFields = value.data;
        })
        .catch(function(alert) {
          vm.alerts = [];
          vm.alerts.push(alert);
        });
    }

    function appendCustomField() {
      vm.alerts = [];
      vm.customFields.push({
        order: vm.customFields.length
      });
    }

    function preview(data) {
      var modal = $uibModal.open({
        animation: true,
        templateUrl: 'customFieldPreview.html',
        controller: function($uibModalInstance) {
          var vm = this;
          vm.data = data;
          vm.cancel = function() {
            $uibModalInstance.dismiss('cancel');
          };
        },
        controllerAs: 'vm'
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('FillCustomFieldsController', FillCustomFieldsController);

  /** @ngInject */
  function FillCustomFieldsController(
    $log,
    $stateParams,
    projectCustomField
  ) {
    var vm = this;
    var projectId = $stateParams.projectId;

    angular.element(document).ready(init);

    function init() {
      projectCustomField
        .getMemberDataByProjectId(projectId)
        .then(function(value) {
          vm.data = value.data
        })
        .catch(function(alert) {
          vm.alert = [alert];
        });
    }

    vm.update = function() {
      $log.debug('=== vm.update() ===');
      $log.debug(vm.data);

      var updatedData = [];

      angular.forEach(vm.data, function(item) {
        $log.debug(item);

        var value = item.custom_field_data;

        if (value != null) {
          $log.debug('value');
          $log.debug(value);

          delete value.created_at;
          delete value.updated_at;

          value['custom_field_id'] = item.id;
        }

        this.push(value);
      }, updatedData);

      $log.debug(updatedData);

      var onSuccess = function(response) {
        $log.debug(response);
        vm.msg = '已成功送出。'
      };
      var onFailure = function(response) {
        $log.debug(response);
      };

      projectCustomField
        .storeOrUpdateMemberDataByProjectId(updatedData, projectId)
        .then(onSuccess)
        .catch(onFailure);
    };

    vm.remove = function() {};
  }
})();

